
.qcw-init-config label{display:block;font-size:14px
}
.qcw-init-config input,.qcw-init-config button,.qcw-init-config textarea{display:inline-block;width:100%;padding:5px 7px;border:1px solid #ddd;border-radius:5px;margin:5px 0 15px 0
}
.qcw-init-config small{display:block;margin-top:-7px;font-size:10px;margin-bottom:15px
}
.qcw-init-config button{background:#8bc;cursor:pointer;color:#FFF;border:0;padding:15px;font-weight:bold;text-shadow:0 -1px 0 rgba(0,0,0,0.3)
}
.qcw-config-form{padding:20px
}



