
.qcw-comment .avatar{border:2px solid #3EC4AF;min-width:30px;margin-right:15px;position:relative;z-index:2;width:50px !important;height:50px !important
}
.qcw-comment .avatar img{width:50px !important;height:50px !important
}
.qcw-comment{display:flex;position:relative;font-size:12px
}
.qcw-comment.welcome:before{border:0
}
.qcw-comment.comment--me{justify-content:flex-end
}
.qcw-comment__message{padding-top:5px;line-height:1.7em;min-height:30px;padding-bottom:15px;margin-bottom:5px;width:100%;max-width:200px;background:#cfe4eb;padding:10px;border-radius:0 15px 15px 15px;position:relative;word-break:break-word
}
.comment--me .qcw-comment__message{background:#8bc;color:#FFF;border-radius:15px 0 15px 15px
}
.comment--me .qcw-comment__message:after{left:100%;top:0;border:solid transparent;content:" ";height:0;width:0;position:absolute;pointer-events:none;border-color:rgba(238,238,238,0);border-left-color:#8bc;border-width:15px;margin-top:-15px;margin-left:-23px;transform:rotate(-135deg)
}
.qcw-comment__message:after{left:-10px;top:0;border:solid transparent;content:" ";height:0;width:0;position:absolute;pointer-events:none;border-color:rgba(238,238,238,0);border-left-color:#cfe4eb;border-width:15px;margin-top:-15px;transform:rotate(-45deg)
}
.welcome__message{padding-top:5px;line-height:1.7em;min-height:68px;padding-bottom:15px;margin-bottom:15px;width:100%;background:#F4F4F4;padding:10px;border-radius:5px;position:relative;box-shadow:0 3px 5px rgba(0,0,0,0.15)
}
.qcw-comment__message p{margin:0
}
.qcw-comment__info{display:flex;justify-content:space-between;border-bottom:1px dotted #ccc;margin-bottom:7px
}
.qcw-comment__username{font-weight:bold;line-height:1;color:#444
}
.comment--me .qcw-comment__username{color:#FFF
}
.qcw-comment__time{font-size:10px;color:#444
}
.comment--me .qcw-comment__time{color:#FFF
}
.qcw-image-container{height:100px;overflow:hidden;margin:-8px -10px -10px -10px;border-radius:0 0 5px 5px
}
.qcw-image-container img{min-width:200px;min-height:110px;cursor:-webkit-zoom-in
}
.qcw-image-container img:hover{opacity:1
}
.qcw-file-container a{display:flex;color:#2c3e50
}
.qcw-file-container i.fa{position:relative;font-size:3em;color:#2c3e50;float:left
}
.qcw-file-container i.fa .ft{position:absolute;top:10px;margin-left:5px;font-size:12px;background:transparent;font-family:sans-serif
}
.qcw-file-container i.fa .fe{font-size:16px;background:#2c3e50;color:#fff;padding:2px 6px 4px 4px;margin-top:-12px;margin-left:10px;border-radius:2px;font-family:sans-serif
}
.qcw-file-container .file-name{word-break:break-word;line-height:1.4;float:left;padding-top:5px;padding-left:5px
}



